<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
      return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {
        }
      }
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let that = this
        let xAxisData = [];
        let option = {
          baseOption: {
            backgroundColor: '#fff',
            tooltip: {
              trigger: 'axis',
              axisPointer: { // 坐标轴指示器，坐标轴触发有效
                type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
                shadowStyle: {
                  color: '#eeeff1',
                  shadowBlur: 0,
                  shadowColor: '#ff9014',
                  shadowOffsetY: -5
                },
                z: 0
              },

              backgroundColor: 'transparent',
              formatter: function (param) {
                //console.log(param)
                let html = '';
                return html += '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">' +
                  '<p style="font-size:16px;font-weight:bold;">' + param[0].name +

                  '：<span stype="color:#ff7200;font-size:20px;">' + param[0].value + '</span>' +
                  '</p>' +
                  '<i style="position:absolute;bottom:-12px;left:48%;width:0;height:0;border:6px solid transparent;border-top:6px solid rgba(211,221,231,.9);"></i>' +
                  '<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i>' +
                  '</div>'
              },
              padding: 10
            },
            timeline: {
              data: this.echartsData.timeLinedata,
              left: 30,
              right: 30,
              bottom: 0,
              symbol: 'circle',
              symbolSize: 15,
              symbolRotate: '30',
              playInterval: '2000',
              loop: true,
              axisType: 'category',
              itemStyle: {
                normal: {
                  color: '#fff',
                  borderColor: '#008aff'
                },
                emphasis: {
                  color: '#fff'
                }
              },
              lineStyle: {
                color: '#52b2ff',
                width: 5
              },
              checkpointStyle: {
                color: '#008aff',
                symbolSize: 18,
                borderColor: 'rgba(255, 255, 255, 0.9)',
                animationDuration: '1000'
              },
              controlStyle: {
                show: true
              },
              autoPlay: true,
            },
            legend: {
              show: false,
              data: ['毛利率', '营业收入（元）']
            },
            grid: {
              left: '4%',
              right: '4%',
              bottom: '20%',
              top: '10%',
              containLabel: true
            },
            xAxis: [{
              type: 'category',
              data: this.echartsData.xAxisData,
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#cccccc',
                  width: 2,
                }
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                textStyle: {
                  color: '#808080',
                  fontSize: 10
                }
              }
            }],
            yAxis: [
              {
                type: 'value',
                z: 2,
                //interval: 4000,
                gridIndex: 0,
                axisLabel: {
                  textStyle: {
                    color: '#808080',
                    fontSize: 10
                  }
                },
                axisLine: {
                  show: true,
                  lineStyle: {
                    color: '#cccccc',
                    width: 1,
                  }
                },
                axisTick: {
                  show: false,
                },
                splitLine: {
                  show: true,
                  lineStyle: {
                    color: '#f5f5f5',
                    type: 'solid',
                  }
                }
              }
            ],
            series: [{
              name: '总量',
              type: 'bar',
              stack: '总量',
              yAxisIndex: 0,
              barMaxWidth: 20,
              markArea: {
                silent: true,
                data: [
                  [{
                    name: '',
                    xAxis: xAxisData[-1],
                    itemStyle: {
                      //normal: {
                      color: new that.$echarts.graphic.LinearGradient(
                        0, 0, 0, 1,
                        [
                          {offset: 0, color: 'rgba(255,255,255,.5)'},
                          {offset: 1, color: 'rgba(229,243,255,.5)'}
                        ]
                      )
                      // }
                    },
                  }, {
                    xAxis: xAxisData[6],
                  }]

                ]
              },
              itemStyle: {
                normal: {
                  color: new that.$echarts.graphic.LinearGradient(
                    0, 0, 0, 1, [{
                      offset: 0,
                      color: '#18a4e5'
                    },
                      {
                        offset: 1,
                        color: '#2881c8'
                      }
                    ]
                  )
                },
                emphasis: {
                  color: new that.$echarts.graphic.LinearGradient(
                    0, 0, 0, 1, [{
                      offset: 0,
                      color: '#ff9a16'
                    },
                      {
                        offset: 1,
                        color: '#ff8212'
                      }
                    ]
                  )
                }
              },
              //data: [20, 30, 40, 20, 30, 90, 50, 60]
              data: this.echartsData.seriesData
            }
            ]

          },
          options: [{
            series: [{
              name: '企业税收额',
              type: 'bar',
              stack: '总量',

              data: [50, 32, 50, 34, 60, 80]
            }]
          },
            {
              series: [{
                name: '企业税收额',
                type: 'bar',
                stack: '总量',

                data: [60, 52, 50, 54, 90, 30]
              }]
            },

            {
              series: [{
                name: '企业税收额',
                type: 'bar',
                stack: '总量',

                data: [40, 30, 10, 34, 70, 70]
              }]
            },
            {
              series: [{
                name: '企业税收额',
                type: 'bar',
                stack: '总量',

                data: [50, 32, 50, 34, 60, 80]
              }]
            },
            {
              series: [{
                name: '企业税收额',
                type: 'bar',
                stack: '总量',

                data: [60, 52, 50, 54, 90, 30]
              }]
            },

            {
              series: [{
                name: '企业税收额',
                type: 'bar',
                stack: '总量',

                data: [40, 30, 10, 34, 70, 70]
              }]
            }

          ]
        }
        this.myChart.setOption(option)
      }
    },
    watch: {}
  }
</script>

<style lang="scss" scoped>
  .echarts {
    width: 100%;
    height: 100%;
  }
</style>



