<template>
  <div id="chart7"></div>
</template>
<script>
import echarts from 'echarts'
export default {
  methods: {
    createChart () {
      let chart = echarts.init(document.getElementById('chart7'))
      let xAxisData = ['2008', '2009', '2010', '2011', '2012', '2013', '2014', '2015']
      let option = {
        backgroundColor: '#fff',
        tooltip: {
          trigger: 'axis',
          // backgroundColor: 'rgba(0,0,0,0.9)',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
            shadowStyle: {
              color: '#eeeff1',
              shadowBlur: 0,
              shadowColor: '#ff9014',
              shadowOffsetY: -5
            },
            z: 0
          },
          backgroundColor: 'transparent',
          formatter: function (param) {
            var html = ''
            html += '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">' +
              '<p style="font-size:16px;font-weight:bold;">' +
              '<span stype="color:#ff7200;font-size:20px;">' + param[0].data + '家企业</span>' +
              '</p>' +
              '<p style="font-size:16px;font-weight:bold;">' +

              '<span stype="color:#ff7200;font-size:20px;">' + param[0].name + '年</span>' +
              '</p>' +
              // '<i style="position:absolute;bottom:-12px;left:48%;width:0;height:0;border:6px solid transparent;border-top:6px solid #28affc;"></i>' +
              // '<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i>' +
              '</div>'
            return html
          },
          padding: 10
        },
        legend: {
          data: ['每日收盘价', '成交量'],
          top: '10'
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: xAxisData,
          axisLine: {
            show: true,
            lineStyle: {
              color: '#cccccc',
              width: 2
            }
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            color: '#666',
            textStyle: {
              color: '#808080',
              fontSize: 10
            }
          }
        }],
        yAxis: [
          {
            type: 'value',
            z: 2,
            name: '数量',
            interval: 4000,
            gridIndex: 0,
            axisLabel: {
              color: '#666',
              textStyle: {
                color: '#808080',
                fontSize: 10
              },
              // formatter: function (value) {
              //   var v
              //   if (value >= 10000) {
              //     v = (value / 10000) + '亿'
              //     return v
              //   } else {
              //     v = value + '万'
              //     return v
              //   }
              // }
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#cccccc',
                width: 1
              }
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: '#f5f5f5',
                type: 'solid'
              }
            }
          }
        ],
        series: [{
          name: '成交量',
          type: 'bar',
          stack: '总量',
          yAxisIndex: 0,
          barMaxWidth: 20,
          markArea: {
            silent: true,
            data: [
              [{
                name: '',
                xAxis: xAxisData[-1],
                itemStyle: {
                  normal: {
                    color: new echarts.graphic.LinearGradient(
                      0, 0, 0, 1,
                      [
                        { offset: 0, color: 'rgba(255,255,255,.5)' },
                        { offset: 1, color: 'rgba(229,243,255,.5)' }
                      ]
                    )
                  }
                }
              }, {
                xAxis: xAxisData[6]
              }]

            ]
          },
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(
                0, 0, 0, 1, [{
                  offset: 0,
                  color: '#18a4e5'
                },
                {
                  offset: 1,
                  color: '#2881c8'
                }
                ]
              )
            },
            emphasis: {
              color: new echarts.graphic.LinearGradient(
                0, 0, 0, 1, [{
                  offset: 0,
                  color: '#ff9a16'
                },
                {
                  offset: 1,
                  color: '#ff8212'
                }
                ]
              )
            }
          },
          data: [2000, 3000, 4000, 20000, 3000, 9000, 2000, 5000]
        }
        ]

      }
      chart.setOption(option)
    }
  },
  mounted () {
    this.createChart()
  }
}
</script>
<style>
#chart7 {
  height: 100%;
}
</style>



