<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
          let that = this
          let xAxisData = this.echartsData.xAxisData
          let option = {
            backgroundColor: '#fff',
            tooltip: {
              trigger: 'axis',
              axisPointer: { // 坐标轴指示器，坐标轴触发有效
                type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
                shadowStyle: {
                  color: '#eeeff1',
                  shadowBlur: 0,
                  shadowColor: '#ff9014',
                  shadowOffsetY: -5
                },
                z:0
              },

              backgroundColor: 'transparent',
              formatter: function(param) {
               /* console.log(param)*/
                var v;
                var html = '';
                if (param[0].value >= 10000) {
                  v = (param[0].value / 10000) + '亿'

                } else {
                  v = param[0].value + '万'

                }
                return html += '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">'+
                  '<p style="font-size:14px;color:#666;">毛利率：'+
                  '<span style="color:#ff7200;font-size:16px;">'+param[1].value+'%</span>'+
                  '</p>'+
                  '<p style="font-size:14px;color:#666;">营业收入：'+

                  '<span style="color:#ff7200;font-size:16px;">'+v+'</span>'+
                  '</p>'+
                  '<i style="position:absolute;bottom:-12px;left:47.5%;width:0;height:0;border:6px solid transparent;border-top:6px solid rgba(211,221,231,.6);"></i>'+
                  '<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i>'+
                  '</div>'
              },
              padding: 10
            },
            legend: {
              data: ['毛利率','营业收入（元）'],
              itemWidth:14,
              itemHeight:9,
              top:10,
              left:'center',
              itemGap: 30,
              orient:'horizontal',
              textStyle:{
                color:'#333'
              }
            },
            grid: {
              left: '4%',
              right: '1%',
              bottom: '10%',
              top: '20%',
              containLabel: true
            },
            xAxis: [{
              type: 'category',
              data: xAxisData,
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#cccccc',
                  width: 2,
                }
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                textStyle: {
                  color: '#808080',
                  fontSize: 10
                }
              },
            }],
            yAxis: [
              {
                type: 'value',
                z: 2,
                interval: 4000,
                gridIndex: 0,
                axisLabel: {
                  textStyle: {
                    color: '#808080',
                    fontSize: 10
                  },
                  formatter: function(value) {
                    let v
                    if (value >= 10000) {
                      v = (value / 10000) + '亿'
                      return v
                    } else {
                      v = value + '万'
                      return v
                    }
                  }
                },
                axisLine: {
                  show: true,
                  lineStyle: {
                    color: '#cccccc',
                    width: 1,
                  }
                },
                axisTick: {
                  show: false,
                },
                splitLine: {
                  show: true,
                  lineStyle: {
                    color: '#f5f5f5',
                    type: 'solid',
                  }
                }
              },
              {
                type: 'value',
                z: 2,
                show: false,
                axisLabel: {
                  textStyle: {
                    color: '#808080',
                    fontSize: 10
                  },
                  formatter: '{value}',
                },
                axisLine: {
                  show: true,
                  lineStyle: {
                    color: '#cccccc',
                    width: 1,
                  }
                },
                axisTick: {
                  show: false,
                },
                splitLine: {
                  show: true,
                  lineStyle: {
                    color: '#f5f5f5',
                    type: 'solid',
                  }
                }
              }
            ],
            series: [{
              name: '营业收入（元）',
              type: 'bar',
              stack: '总量',
              yAxisIndex: 0,
              barMaxWidth: 14,
              markArea: {
                silent: true,
                data: [
                  [{
                    name: '',
                    xAxis: xAxisData[-1],
                    itemStyle: {
                        color: new that.$echarts.graphic.LinearGradient(
                          0, 0, 0, 1,
                          [
                            {offset: 0, color: 'rgba(255,255,255,.5)'},
                            {offset: 1, color: 'rgba(229,243,255,.5)'}
                          ]
                        )
                    },
                  }, {
                    xAxis: xAxisData[7],
                  }]

                ]
              },
              itemStyle: {
                normal: {
                  color: new that.$echarts.graphic.LinearGradient(
                    0, 0, 0, 1, [{
                      offset: 0,
                      color: '#18a4e5'
                    },
                      {
                        offset: 1,
                        color: '#2881c8'
                      }
                    ]
                  )
                },
                emphasis: {
                  color: new that.$echarts.graphic.LinearGradient(
                    0, 0, 0, 1, [{
                      offset: 0,
                      color: '#ff9a16'
                    },
                      {
                        offset: 1,
                        color: '#ff8212'
                      }
                    ]
                  )
                }
              },
              data: this.echartsData.seriesData[0]
            },
              {
                name: '毛利率',
                type: 'line',
                yAxisIndex: 1,
                symbol: 'circle',
                lineStyle: {
                  normal: {
                    color: '#ff8212'
                  }
                },
                itemStyle: {
                  normal: {
                    color: '#ff8212',
                    borderColor: '#fff',
                    borderWidth: '1',
                    label: {
                      show: false,
                      position: 'top',
                      color: '#ff7200',
                      fontSize: 14,
                      formatter: '{c}%'
                    }
                  },
                },

                data: this.echartsData.seriesData[1],
                symbolSize: 8
              }
            ]

          }
          this.myChart.setOption(option)
        }
    },
    watch: {
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:100%;
  }
</style>



