<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
        let that = this
        let arr = [];
        this.echartsData.seriesData.forEach(function(v,i){
            arr.push(v.value)
        })
        let maxData = arr.sort(function(a,b){
            return b-a
          })[0]*1.2
        let maxArr = [maxData, maxData, maxData, maxData, maxData]
        let rich = {
          yellow: {
            color: '#fff',
            fontSize: 14,
            padding: [5, 8, 5, 8],
            align: 'center',
            backgroundColor:'#f5531a'
          },
          color: {
            color: "#fff",
            fontSize: 14,
            padding:[5,8,5,8],
            align: 'center',
            backgroundColor:'#ff7e00'
          },
          color1: {
            color: "#fff",
            fontSize: 14,
            padding:[5,8,5,8],
            align: 'center',
            backgroundColor:'#ffc500'
          },
          color2: {
            color: "#fff",
            fontSize: 14,
            padding:[5,8,5,8],
            align: 'center',
            backgroundColor:'#69da2d'
          },
          color3: {
            color: "#fff",
            fontSize: 14,
            padding:[5,8,5,8],
            align: 'center',
            backgroundColor:'#00a2ff'
          },
          black: {
            color: '#474747',
            fontSize: 12,
            padding:[0,10,0,10],
            align: 'right'
          }
        }
        let option = {
          backgroundColor:'#fff',
          grid:{
            top:90,
            left:23,
            right:23,
            bottom:10
          },
          xAxis:  {
            show:false,
            type: 'value',
            nameTextStyle:{color:'#8199c3'},
            splitLine:{ show:false },
            axisTick:{ show:false },
            axisLabel:{ color:'#8199c3' },
            axisLine:{
              lineStyle:{
                color:"#18418a"
              }
            },
          },
          yAxis: {
            show:false,
            type: 'category',
            data: ['','','','',''],
            nameTextStyle:{color:'#8199c3'},
            splitLine:{ show:true },
            axisTick:{ show:false },

            axisLine:{
              show:false,
              lineStyle:{
                color:"#18418a"
              }
            }
          },
          series: [
            {
              name: '访问次数',
              type: 'bar',
              barGap:'-100%',
              barWidth:10,
              z: 2,
              itemStyle:{
                normal:{
                  color:'#f0f0f0'
                },
                emphasis: {
                  color: '#f0f0f0'
                }
              },
              data: [60,60,60,60,60]

            },
            {
              name: '访问次数',
              type: 'bar',
              barWidth:10,
              z: 3,
              label: {
                normal: {
                  show: true,
                  position: [0,-28],
                  formatter:function(params){
                    if(params.data.value1=="1"){
                      return '{yellow|'+params.data.value1+'}{black|'+params.data.name+'}'
                    }else if(params.data.value1=="2"){
                      return '{color|'+params.data.value1+'}{black|'+params.data.name+'}'
                    }else if(params.data.value1=="3"){
                      return '{color1|'+params.data.value1+'}{black|'+params.data.name+'}'
                    }else if(params.data.value1=="4"){
                      return '{color2|'+params.data.value1+'}{black|'+params.data.name+'}'
                    }else if(params.data.value1=="5"){
                      return '{color3|'+params.data.value1+'}{black|'+params.data.name+'}'
                    }

                  },
                  rich:rich
                }
              },
              itemStyle: {
                  color: '#14aaff'
              },
              emphasis: {
                itemStyle: {
                  color: '#14aaff'
                },
              },
              data: this.echartsData.seriesData
            }

          ]
        };

        this.myChart.setOption(option)
        }
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:100%;
  }
</style>



