<template>
  <div id="chart2"></div>
</template>
<script>
import echarts from 'echarts'
export default {
  data () {
    return {}
  },
  methods: {
    createChart () {
      var chart = echarts.init(document.getElementById('chart2'))
      let xAxisData = ['2017-11-19', '2017-11-20', '2017-11-21', '2017-11-22', '2017-11-23', '2017-11-24', '2017-11-25', '2017-11-26', '2017-11-27', '2017-11-28', '2017-11-29', '2017-11-30', '2017-12-01']
      let option = {
        backgroundColor: '#ffffff',
        name: 'a',
        tooltip: {
          trigger: 'axis', //按x轴显示
          show: true,
          axisPointer: {
            lineStyle: {
              color: 'none' //不显示线条
            }
          },
          backgroundColor: 'rgba(255, 255, 255, 0.9)',
          textStyle: {
            align: 'left',
            fontSize: 12,
            color: '#333333'
          },
          extraCssText: 'box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.2)' //添加阴影
        },
        color: ['#0580f2', '#faa732', '#e1575d'],
        grid: {
          left: '5%',
          right: '6%',
          top: '14%',
          bottom: '6%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisTick: {
            show: false
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#ccc'
            }
          },
          axisLabel: {
            color: '#6a6a6a',
            textStyle: {
              fontSize: 14
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: '#fff'
              }, {
                offset: 1,
                color: '#f3faff'
              }]),
              z: 0
            }
          },
          data: xAxisData
        },
        yAxis: {
          type: 'value',
          name: '单位(万元)',
          axisLabel: {
            color: '#6a6a6a',
            textStyle: {
              fontSize: 14
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#ccc'
            }
          },
          axisTick: {
            show: false
          },
          z: 1
        },
        series: [{
          name: '发明专利',
          type: 'line',
          symbol: 'circle',
          itemStyle: {
            emphasis: {
              symbol: 'circle'
            }
          },
          smooth: true,
          symbolSize: [8, 8],
          data: [10000, 0, 10000, 42342, 102345, 8786, 0, 10000, 42342, 102345, 8786, 0, 10000, 42342, 102345, 8786]
        }]
      }
      chart.setOption(option)
    }
  },
  mounted () {
    this.createChart()
  }
}
</script>
<style>
#chart2 {
  height: 100%;
}
</style>



