<template>
	<div id="">
		<ul>
			<router-link class="item" tag="li" to="/accurateInvestment/newlyBorn">
				全国新生企业
			</router-link>
			<router-link class="item" tag="li" to="/accurateInvestment/beListed">
				全国上市企业
			</router-link>
			<router-link class="item" tag="li" to="/accurateInvestment/kindsOfPlan">
				全国各类计划企业
			</router-link>
			<router-link class="item" tag="li" to="/accurateInvestment/knownCelebrity">
				全国名人名企
			</router-link>
			<router-link class="item" tag="li" to="/accurateInvestment/blackEnterprise">
				全国黑色企业
			</router-link>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'navMenu',
	}
</script>

<style lang="scss" scoped>
div {
	padding: 20px 0;
	ul {
			width: 100%;
			display: flex;
			/*justify-content: flex-end;*/
			li {
				color: #015293;
				padding: 13px 0;
				font-size: 16px;
				cursor: pointer;
				flex: 1;
				text-align: center;
				box-sizing: border-box;
				border: 1px solid #d5d5d5;
				background: linear-gradient(to bottom, #f6f6f6, #dfdfdf);
				&.active {
					background: linear-gradient(to bottom, #0f82c4, #015293);
					border: 1px solid #015293;
					color: #fff;
				}
				&:first-of-type{
					border-radius: 5px 0 0 5px;
				}
				&:last-of-type{
					border-radius: 0 5px 5px 0;
				}
			}
	}
}

</style>


