<template>
  <div id="chart4"></div>
</template>
<script>
import echarts from 'echarts'
export default {
  data () {
    return {}
  },
  methods: {
    createChart () {
      var chart = echarts.init(document.getElementById('chart4'))
      let option = {
        backgroundColor: '#fff',
        title: {
          text: '155',
          top: '40%',
          left: 'center',
          textStyle: {
            fontSize: 24,
            color: '#5681ad'
          },
          subtext: '较强景气',
          subtextStyle: {
            color: '#5681ad',
            fontSize: 14
          }
        },
        tooltip: {
          backgroundColor: '#fff',
          padding: 10,
          textStyle: {
            color: '#666'
          }
        },
        grid: {
          left: 0,
          right: 0,
          top: 0,
          bottom: 2,
          containLabel: true
        },
        radar: {
          shape: 'circle',
          splitNumber: 4,
          startAngle: -60,
          axisLine: {            // 坐标轴线
            show: true,        // 默认显示，属性show控制显示与否
            lineStyle: {
              width: 3,
              color: '#3d3d3d',
              shadowColor: 'rgba(61, 61, 61, .28)',
              shadowBlur: 10,
              opacity: 0.08
            }
          },
          indicator: [
            { name: '人才需求' },
            { name: '资本活跃度' },
            { name: '创新能力' },
            { name: '供需情况' },
            { name: '行业保险系数' },
            { name: '企业增长' }
          ],
          splitLine: {
            lineStyle: {
              color: ['rgba(226, 226, 226, .2)', 'rgba(216, 214, 214, .2)'],
              width: 2
            },
            shadowColor: 'rgba(0, 0, 0, 1)',
            shadowBlur: 10
          },
          splitArea: {
            areaStyle: {
              color: ['rgba(226, 226, 226, .2)', 'rgba(243, 243, 243, .2)']
            }
          },
          name: {
            textStyle: {
              color: 'rgba(71, 71, 71,1)',
              fontSize: 16
            },
            formatter: function (param) {
              return param
            }
          }
        },
        series: [{
          name: '',
          type: 'radar',
          symbol: 'circle',
          symbolSize: 6,
          symbolOffset: [0, '50%'],
          data: [
            {
              value: [25000, 15000, 5000, 15000, 25000, 20000],
              lineStyle: {
                normal: {
                  type: 'solid',
                  color: 'rgba(1, 82, 147, .5)',
                  width: '2'
                }
              },
              areaStyle: {
                normal: {
                  color: 'rgba(60, 162, 240, 0.2)'
                }
              },
              itemStyle: {
                normal: {
                  color: '#14aaff'
                }
              },
              name: '市场评分'
            }
          ]
        }]
      }
      chart.setOption(option)
    }
  },
  mounted () {
    this.createChart()
  }
}
</script>
<style>
#chart4 {
  height: 100%;
}
</style>



