<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
      return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {
        }
      }
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          tooltip: {
            axisPointer: { // 坐标轴指示器，坐标轴触发有效
              type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
              shadowStyle: {
                color: '#eeeff1',
                shadowBlur: 0,
                shadowColor: '#ff9014',
                shadowOffsetY: -5
              },
              z: 0
            },
            backgroundColor: 'transparent',
            formatter: function (param) {
              let html = '';
              return html += '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">' +
                '<p style="font-size:16px;font-weight:bold;">' + param.data.name +

                '：<span stype="color:rgba(211,221,231,.9);font-size:20px;">' + param.data.value + '</span>' +
                '</p>' +
                '</div>'
            }
          },
          legend: {
            x: '10%',
            y: '40',
            itemWidth: 15,
            itemHeight: 10,
            //data: ['新华网', '人民网','新浪微博', '洛阳网', '长城网', '微信公众号' ]
            data: this.echartsData.legendData
          },
          "color": ["#ff8d07", "#ffcd00", "#08e1f8", '#17bdfe', '#1e88f1', '#356bb9', "transparent"],
          "startAngle": '180',
          series: [{
            name: '',
            type: 'pie',
            radius: ['20%', '260%'],
            avoidLabelOverlap: false,
            startAngle: 0,
            roseType: 'radius',
            center: ["50%", "50%"],
            //stillShowZeroSum:0,
            label: {
              normal: {
                show: true,
                position: 'inside',
                formatter: function (a) {
                  return a.value
                }/*,
                    textStyle: {
                        color: '#333',
                        fontSize: 12
                    }*/
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '12',
                  fontWeight: 'bold'
                }
              }
            },
            labelLine: {
              normal: {
                show: false,
                length: 0,
                length2: 0,
              }
            },
            /*data: [{
              value: 100,
              name: '新浪微博',

            }, {
              value: 120,
              name: '微信公众号'
            }, {
              value: 150,
              name: '新华网'
            }, {
              value: 180,
              name: '人民网'
            }, {
              value: 220,
              name: '洛阳网'
            }, {
              value: 300,
              name: '长城网'
            }, {
              value: 1070,
              name: '',
              label: {
                normal: {
                  show: false
                },
                emphasis: {
                  show: false
                }
              },
              tooltip: {
                formatter: function(a) {
                  return ""
                }
              }
            }]*/
            data: this.echartsData.seriesData
          },

          ]
        }
        this.myChart.setOption(option)
      }
    },
    watch: {}
  }
</script>

<style lang="scss" scoped>
  .echarts {
    width: 100%;
    height: 100%;
    /*background:transparent !important;*/
  }
</style>



