<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
      return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {
        }
      }
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let that = this
        let rich = {
          yellow: {
            color: '#ff7e00',
            fontSize: 14,
            padding: [0, 0, 0, -30],
            align: 'left'
          },
          black: {
            color: '#666666',
            fontSize: 12,
            padding: [0, 0, 0, -30],
            align: 'left'
          }
        }
        let option = {
          tooltip: {
            show: false,
            formatter: function (params) {
              if (params.seriesIndex != "0" && params.seriesIndex != "1" && params.seriesIndex != "2") {
                return "{a} <br/>{b} : {c} ({d}%)";
              }
            }
          },
          backgroundColor: '#fff',
          legend: {
            show: false,
            x: 20,
            y2: 20,
            orient: 'vertical',
            //data: ["行政处罚", "失信", "法律诉讼"]
            data: this.echartsData.legendData
          },
          color: ['#ff8c14', '#2092d6', '#03baff', '#3467b3', '#57d22c', '#ffc800'],
          calculable: false,
          series: [{
            name: '注销企业数量',
            type: 'pie',
            center: ['50%', '50%'],
            radius: ['25%', '45%'],
            label: {
              normal: {
                position: 'outside',
                show: true,
                formatter: function (params) {
                  return '{yellow|' + params.value + '%}\n{black|' + params.name + '}'
                },
                textStyle: {
                  color: "#333",
                  fontSize: 14
                },
                padding: [3, -40, 45, -20],
                rich: rich
              },
              emphasis: {
                position: 'outside',
                show: true,
                formatter: function (params) {
                  return '{yellow|' + params.value + '%}\n{black|' + params.name + '}'
                },
                textStyle: {
                  color: "#333",
                  fontSize: 14
                },
                rich: rich
              }
            },
            itemStyle: {
              normal: {
                borderColor: '#fff',
                borderWidth: 3
              }
            },
            labelLine: {
              normal: {
                textStyle: {
                  fontSize: 14
                },
                length: 5,
                length2: 60,
                lineStyle: {
                  width: 2,
                  color: '#ebebeb'
                }
              },
              emphasis: {
                textStyle: {
                  fontSize: 14
                },
                length: 5,
                length2: 60,
                lineStyle: {
                  width: 2,
                  color: '#ebebeb'
                }
              }
            },
            // data: [{value: 25, name: '行政处罚',}, {value: 35,name: '失信'}, {value: 40,name: '法律诉讼'}]
            data: this.echartsData.seriesData
          }]
        }
        this.myChart.setOption(option)
      }
    },
    watch: {}
  }
</script>

<style lang="scss" scoped>
  .echarts {
    width: 100%;
    height: 100%;
  }
</style>



