<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
          let that = this;
          let option = {
            backgroundColor: '#fff',
            legend: {
              top: 0,
              left: 'center',
              itemGap: 10,
              orient: 'horizontal',
              textStyle: {
                color: '#333'
              },
              itemWidth: 20,
              itemHeight: 10,
              data: this.echartsData.legendData
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: { // 坐标轴指示器，坐标轴触发有效
                type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
                shadowStyle:{
                  color: '#eeeff1',
                  shadowBlur: 0,
                  shadowColor: '#ff9014',
                  shadowOffsetY: -5
                },
                z: 0
              },
              backgroundColor: 'transparent',
              formatter: function (params){
                // console.log(params);
                return '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">'
                  +'<span style="color:#008ef2;font-weight:bold; font-size:16px">'+ params[0].name +'</span>' + '<br/>'
                  + '<span style="color:#666;">'+params[0].seriesName + ' : </span>' +'<span style="color:#3a7fdd;font-weight:bold;">'+ params[0].value +'</span>'+ '<br/>'
                  + '<span style="color:#666;">'+params[1].seriesName + ' : </span>' +'<span style="color:#ff9916;font-weight:bold;">'+params[1].value+'</span>'+ '<br/>'
                  +'<i style="position:absolute;bottom:-12px;left:47.5%;width:0;height:0;border:6px solid transparent;border-top:6px solid rgba(211,221,231,.6);"></i>'
                  +'<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i>'+
                  '</div>'
              },
              padding: 10
            },
            grid: {
              left: '3%',
              right: '4%',
              top: '20%',
              bottom: '10%',
              containLabel: true
            },
            xAxis: [{
              type: 'category',
              data: this.echartsData.xAxisData,
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#e4e4e4'
                }
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                textStyle: {
                  color: '#666',
                  fontSize: 10
                }
              }
            }],
            yAxis: [{
              type: 'value',
              z: 2,
              axisLabel: {
                textStyle: {
                  color: '#666',
                  fontSize: 10
                },
                formatter: function(value) {
                  var v = value.toFixed(2)
                  return v
                }
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#e4e4e4'
                }
              },
              axisTick: {
                show: false
              },
              splitLine: {
                show: true,
                lineStyle: {
                  color: '#f5f5f5'
                }
              },
            }],
            series: [
              {
                name: this.echartsData.legendData[1],
                type: 'bar',
                barMaxWidth: 10,
                barGap: 0,
                markArea: {
                  silent: true,
                  data: [
                    [{
                      name: '',
                      xAxis: this.echartsData.xAxisData[-1],
                      itemStyle: {
                        normal: {
                          color: new that.$echarts.graphic.LinearGradient(
                            0, 0, 0, 1,
                            [
                              {offset: 0, color: 'rgba(255,255,255,.5)'},
                              {offset: 1, color: 'rgba(229,243,255,.5)'}
                            ]
                          )
                        }
                      },
                    }, {
                      xAxis: this.echartsData.xAxisData[5],
                    }]

                  ]
                },
                itemStyle: {
                  normal: {
                    color: new that.$echarts.graphic.LinearGradient(
                      0, 0, 0, 1, [{
                        offset: 0,
                        color: '#3a7fdd'
                      },
                        {
                          offset: 1,
                          color: '#315fa4'
                        }
                      ]
                    )
                  }
                },
                data: this.echartsData.seriesData[0]
              },
              {
                name: this.echartsData.legendData[0],
                type: 'line',
                symbol: 'circle',
                lineStyle: {
                  normal: {
                    color: '#ff8105'
                  }
                },
                itemStyle: {
                  normal: {
                    color: '#ff8212',
                    borderColor: '#fff',
                    borderWidth: '1'
                  },
                },
                label: {
                  show: false,
                  position: 'top',
                  color: '#ff8212'

                },
                data: this.echartsData.seriesData[1],
                symbolSize: 8
              }
            ]
          }
          this.myChart.setOption(option)
        }
    },
    watch: {
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:80%;
  }
</style>



