<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
        let that = this
        let option = {
          backgroundColor: '#fff',
          tooltip: {
            /*trigger: 'axis',*/
            axisPointer: {
              lineStyle: {
                color: '#ccc',
                width: 0
              }
            },
            backgroundColor: 'rgba(255,255,255,0.9)',
            formatter: function (params) {
              var html = '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);font-size:14px;font-weight:bold;">'
                html+= '<p><span style="font-size:14px;font-weight:bold;">'+params.seriesName+'</span>：</br><span style="color:#ff7200;font-size:16px;">'+params.value+'%</span></p>'
              html += '<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i></div>'
              return html
            },
            textStyle: {
              color: '#666'
            }
          },
          legend: {
            left: 'center',
            top: '10%',
            icon: 'rect',
            itemWidth: 15,
            itemHeight: 4,
            textStyle: {
              fontSize:14
            },
            data: this.echartsData.legendData
          },

          grid: {
            left: '4%',
            right: '4%',
            bottom: '3%',
            top: '26%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              boundaryGap: false,
              nameTextStyle: {
                color: '#666'
              },
              axisTick: {
                alignWithLabel: true,
                show: false
              },
              axisLabel: {
                textStyle: {
                  color: '#666'
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#ccc'
                }
              },
              splitLine: {
                lineStyle: {
                  color: '#ededed'
                }
              },
              data: this.echartsData.xAxisData,
            }
          ],
          yAxis: [
            {
              name: '单位（家）',
              type: 'value',
              nameTextStyle: {
                color: '#666'
              },
              axisTick: {
                alignWithLabel: true,
                show: false
              },
              axisLabel: {
                textStyle: {
                  color: '#666'
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#ccc'
                }
              },
              splitLine: {
                lineStyle: {
                  color: '#ededed'
                }
              }
            }
          ],
          color: ['#0a93e9' ,'#1eacfc', '#fc8a2d', '#5eaefe', '#6386f8', '#2898ed'],
          series: [
            {
              name: this.echartsData.legendData[0],
              type: 'line',
              z: 3,
              areaStyle: {
                normal: {
                  color: new that.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(24,164,255,.9)'
                  }, {
                    offset: 1,
                    color: 'rgba(40,129,200,.9)'
                  }])

                }
              },
              lineStyle: {
                normal: {
                  width:0
                }
              },
              itemStyle:{
                normal:{
                  symbolSize:1,
                  opacity:0
                },
                emphasis:{
                  symbolSize:4,
                  opacity:1
                }
              },
              data: this.echartsData.seriesData[0]
            },
            {
              name: this.echartsData.legendData[1],
              type:'line',
              z:2,
              itemStyle:{
                normal:{
                  symbolSize:1,
                  opacity:0
                },
                emphasis:{
                  symbolSize:4,
                  opacity:1
                }
              },
              lineStyle: {
                normal: {
                  width: 0
                }
              },
              areaStyle:{
                normal:{
                  color: new that.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(64,194,255,.9)'
                  },
                    {
                      offset: 1,
                      color: 'rgba(0,186,255,.9)'
                    }])

                }
              },
              data: this.echartsData.seriesData[1]
            },
            {
              name: this.echartsData.legendData[2],
              type: 'line',
              z: 1,
              itemStyle: {
                normal: {
                  symbolSize: 1,
                  opacity: 0
                },
                emphasis: {
                  symbolSize: 4,
                  opacity: 1
                }
              },
              lineStyle: {
                normal: {
                  width: 0
                }
              },
              areaStyle:{
                normal:{
                  color: new that.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(255,154,22,.5)'
                  },
                    {
                      offset: 1,
                      color: 'rgba(255,130,18,.5)'
                    }])

                }
              },
              data: this.echartsData.seriesData[2]
            }
          ]

        };
          this.myChart.setOption(option)
        }
    },
    watch: {
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:100%;
  }
</style>



