<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	export default {
		name: 'simpleGraph',
		props: {
			echartsData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {}
		},
		watch: {
			echartsData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let option = {
					series: [{
						type: 'graph',
						layout: 'none',
						symbolSize: 50,
						roam: false,
						silent: true,
						label: {
							normal: {
								show: true
							}
						},
						edgeSymbolSize: [4, 10],
						edgeLabel: {
							normal: {
								textStyle: {
									fontSize: 20
								}
							}
						},
						data: [{
							name: ' ',
							x: 100,
							y: 100,
							ignore: true,
							symbolSize: 20,
							itemStyle: {
								normal: {
									color: new this.$echarts.graphic.LinearGradient(
		                0, 0, 0, 1, [{
		                  offset: 0,
		                  color: '#fff'
		                },
		                {
		                  offset: 1,
		                  color: '#ccc'
		                }
		                ]
		              ),
									borderColor: '#ccc',
									borderWidth: 1,
								}
							},
							categories: -1,
						}, {
							name: 'C行业',
							x: 60,
							y: 110,
							symbolSize: 50,
							itemStyle: {
								normal: {
									color: '#3ca2f0'
								}
							}
						}, {
							name: 'B行业',
							x: 40,
							y: 100,
							symbolSize: 70,
							itemStyle: {
								normal: {
									color: '#007bdd'
								}
							}
						}, {
							name: 'A行业',
							x: 90,
							y: 100,
							symbolSize: 40,
							itemStyle: {
								normal: {
									color: '#015293'
								}
							}
						}, {
							name: 'E行业',
							x: 160,
							y: 110,
							symbolSize: 60,
							itemStyle: {
								normal: {
									color: '#ff7e00'
								}
							}
						}, {
							name: 'D行业',
							x: 140,
							y: 95,
							symbolSize: 40,
							categories: '',
							itemStyle: {
								normal: {
									color: '#f5531a'
								}
							}
						}, {
							name: 'F行业',
							x: 120,
							y: 110,
							categories: '',
							itemStyle: {
								normal: {
									color: '#f5b51a'
								}
							}
						}],
						// links: [],
						links: [{
							source: 'B行业',
							target: ' '
						}, {
							source: 'A行业',
							target: ' '
						}, {
							source: 'C行业',
							target: ' '
						}, {
							source: 'D行业',
							target: ' '
						}, {
							source: 'F行业',
							target: ' '
						}, {
							source: 'E行业',
							target: ' '
						}],
						lineStyle: {
							color: 'source',
							opacity: 0.9,
							curveness: -0.2
						}
					}]
				}

				this.myChart.setOption(option);

			}

		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>


