          line-height: 32px;
          display: flex;
          text-align: left;
          span {
            flex: 1;
            em {
              font-weight: normal;
              color: #999;
            }
          }
        }
        p:first-child {
          height: 36px;
          font-size: 16px;
        }
        .money {
          color: #ff7e00;
          font-size: 24px;
        }
      }
      .control {
        padding: 20px 10px;
        a {
          display: block;
          line-height: 32px;
          color: #015293;
          text-decoration: underline;
        }
      }
    }
  }
  .recent-chart {
    box-sizing: border-box;
    padding: 10px 16px;
    .news-list {
      display: flex;
      text-align: center;
      border-bottom: 1px dashed #ccc;
      line-height: 64px;
      .num {
        flex: 0.6;
        color: #474747;
      }
      .content {
        flex: 6.4;
        text-align: left;
        color: #ababab;
        padding-top: 6px;
        p {
          line-height: 26px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .title {
          color: #474747;
        }
      }
      .date {
        flex: 1.5;
        color: #ababab;
      }
    }
  }
  .potential-chart {
    padding: 28px 24px;
    box-sizing: border-box;
    .potential-item {
      padding-left: 245px;
      position: relative;
      background: #fff9f1;
      margin-bottom: 20px;
      height: 80px;
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
      align-content: space-around;
      .title-wrap {
        box-sizing: border-box;
        width: 148px;
        height: 80px;
        background: #ff5a01;
        position: absolute;
        left: 0;
        z-index: 2;
        color: #fff;
        padding-left: 15px;
        &:after {
          content: '';
          position: absolute;
          right: -60px;
          top: 0;
          border: 40px solid transparent;
          border-left-color: #ff5a01;
          border-left-width: 20px;
        }
        .title {
          font-size: 18px;
          line-height: 44px;
          font-weight: 700;
        }
      }
      .title-label {
        padding-left: 20px;
        width: 36px;
        height: 80px;
        background: #ffdbc2;
        position: absolute;
        left: 148px;
        z-index: 1;
        display: flex;
        align-items: center;
        text-align: right;
        &:after {
          content: '';
          position: absolute;
          right: -60px;
          top: 0;
          border: 40px solid transparent;
          border-left-color: #ffdbc2;
          border-left-width: 20px;
        }
      }
      .potential-label {
        padding: 0 6px;
        height: 20px;
        margin-right: 15px;
        border: 1px solid #f1dcbe;
        float: left;
      }
    }
    .potential-item.second {
      background: #fff9f1;
      .title-wrap {
        background: #ff9800;
        &:after {
          border-left-color: #ff9800;
        }
      }
      .title-label {
        background: #ffe7c1;
        &:after {
          border-left-color: #ffe7c1;
        }
      }
      .potential-label {
        border-color: #f1dcbe;
      }
    }
    .potential-item.third {
      background: #f5fbff;
      .title-wrap {
        background: #049bf1;
        &:after {
          border-left-color: #049bf1;
        }
      }
      .title-label {
        background: #c5e8fc;
        &:after {
          border-left-color: #c5e8fc;
        }
      }
      .potential-label {
        border-color: #c0dcea;
      }
    }
  }
}
.table th.sort {
  position: relative;
  cursor: pointer;
  width:10%;
}

.table th.sort::after, .table th.sort::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
}
.table th.sort::after, .table th.sort::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
}
.table th.sort::before {
  border-right: 4px solid transparent;
  border-bottom: 6px solid #ccc;
  border-left: 4px solid transparent;
  top: 18px;
  right: 10px;
}
.table th.sort::after {
  border-top: 6px solid #ccc;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  top: 25px;
  right: 10px;
}
.table th.just::before {
  border-right: 4px solid transparent;
  border-bottom: 6px solid #015293;
  border-left: 4px solid transparent;
  top: 18px;
  right: 10px;
}
.table th.just::after {
  border-top: 6px solid #ccc;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  top: 25px;
  right: 10px;
}
.table th.reverse::before {
  border-right: 4px solid transparent;
  border-bottom: 6px solid #ccc;
  border-left: 4px solid transparent;
  top: 18px;
  right: 10px;
}
.table th.reverse::after {
  border-top: 6px solid #015293;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  top: 25px;
  right: 10px;
}
</style>



