<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
          let that = this
          let xAxisData = ['2009', '2010', '2011', '2012', '2013', '2014'];
          let option = {
            backgroundColor: '#fff',
            tooltip: {
              confine: true,
              trigger: 'axis',
              backgroundColor:'transparent',
              formatter: function (params){
                // console.log(params);
                return '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">'
                  +'<span style="color:#008ef2;font-weight:bold; font-size:16px">'+ params[0].name +'</span>' + '<br/>'
                  + '<span style="color:#666;font-size:14px;">'+params[0].seriesName + ' : </span>' +'<span style="color:#366fc0;font-weight:bold;">'+ params[0].value +'</span>'+ '<br/>'
                  + '<span style="color:#666;font-size:14px;">'+params[1].seriesName + ' : </span>' +'<span style="color:#1aa0e1;font-weight:bold;">'+params[1].value+'</span>'+ '<br/>'
                  + '<span style="color:#666;font-size:14px;">'+params[2].seriesName + ' : </span>' +'<span style="color:#58d22c;font-weight:bold;">'+params[2].value+'</span>'+ '<br/>'
                  + '<span style="color:#666;font-size:14px;">'+params[3].seriesName + ' : </span>' +'<span style="color:#f5531a;font-weight:bold;">'+params[1].value+'</span>'+ '<br/>'
                  + '<span style="color:#666;font-size:14px;">'+params[4].seriesName + ' : </span>' +'<span style="color:#ff8d14;font-weight:bold;">'+params[2].value+'</span>'+ '<br/>'
                  +'<i style="position:absolute;bottom:-12px;left:47.5%;width:0;height:0;border:6px solid transparent;border-top:6px solid rgba(211,221,231,.6);"></i>'
                  +'<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i>'+
                  '</div>'
              },
              padding: 10
            },
            legend: {
              show: true,
              data: ['主营业务收入', '主营业务利润','所有者所有者权益合计','资产总计','负债总计'],
              top:10,
              left:'center',
              itemGap: 10,
              orient:'horizontal',
              textStyle:{
                color:'#333'
              },
              itemWidth: 20,
              itemHeight: 10,
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '10%',
              containLabel: true
            },
            xAxis: [{
              type: 'category',
              data: xAxisData,
              axisLabel:{
                textStyle:{
                  color:'#808080',
                  fontSize: 12
                }
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#e4e4e4'
                }
              },
              axisTick:{
                show:false
              },
              splitLine:{
                lineStyle:{
                  color:'#f8f8f8'
                }
              }
            }],
            yAxis: [{
              name:'单位（万）',
              nameTextStyle:{
                color:'#808080'
              },
              type: 'value',
              axisLabel:{
                textStyle:{
                  color:'#808080',
                  fontSize: 12
                }
              },
              axisLine: {
                show: true,
                lineStyle: {
                  color: '#e4e4e4'
                }
              },
              axisTick:{
                show:false
              },
              splitLine:{
                lineStyle:{
                  color:'#f8f8f8'
                }
              }
            }],
            series: [{
              name: '主营业务收入',
              type: 'line',
              itemStyle: {
                normal: {
                  color: '#366fc0',
                  borderColor: '#fff',
                  borderWidth: '2',
                }
              },
              symbol:'circle',
              symbolSize:8,
              data: [160, 232, 290, 284, 200, 260]
            },
              {
                name: '主营业务利润',
                type: 'line',
                itemStyle: {
                  normal: {
                    color: '#1aa0e1',
                    borderColor: '#fff',
                    borderWidth: '2',
                  }
                },
                symbol:'circle',
                symbolSize:8,
                data: [530, 542, 600, 534, 490, 470]
              },
              {
                name: '所有者所有者权益合计',
                type: 'line',
                itemStyle: {
                  normal: {
                    color: '#58d22c',
                    borderColor: '#fff',
                    borderWidth: '2',
                  }
                },
                symbol:'circle',
                symbolSize:8,
                data: [320, 332, 360, 424, 530, 570]
              },
              {
                name: '资产总计',
                type: 'line',
                itemStyle: {
                  normal: {
                    color: '#f5531a',
                    borderColor: '#fff',
                    borderWidth: '2',
                  }
                },
                symbol:'circle',
                symbolSize:8,
                data: [440, 452, 510, 534, 570, 590]
              },
              {
                name: '负债总计',
                type: 'line',
                itemStyle: {
                  normal: {
                    color: '#ff8d14',
                    borderColor: '#fff',
                    borderWidth: '2'
                  }
                },
                symbol:'circle',
                symbolSize:8,
                data: [505, 518, 540, 554, 590, 640]
              }
            ]
          }
          this.myChart.setOption(option)
        }
    },
    watch: {
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:100%;
  }
</style>



