<template>
  <div id="listedMap"></div>
</template>
<script>
import echarts from 'echarts'
import china from '../../../assets/js/geojs/china.json'
export default {
  methods: {
    createChart () {
      let chart = echarts.init(document.getElementById('listedMap'))
      echarts.registerMap('china', china)
      let option = {
        backgroundColor: '#fff',
        /* tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(0,0,0,.5)',
          show: true,
          padding: [10, 18, 10, 18],
          formatter: function (datas) {
            return datas.value + '<br/>' + datas.name
          },
          textStyle: {
            color: '#fff',
            align: 'center'
          }

        }, */
        visualMap: {
          type: 'piecewise',
          pieces: [
            { gt: 1500 },
            { gt: 900, lte: 1500 },
            { gt: 310, lte: 1000 },
            { gt: 200, lte: 300 },
            { gt: 0, lte: 200 }
          ],
          left: 'left',
          bottom: '5%',           // 文本，默认为数值文本
          calculable: true,
          padding: [0, 0, 0, 50],
          color: ['#2998FF', '#4EAAFF', '#64C3FF', '#CEE7FF'],
          itemWidth: 10,
          textStyle: {
            color: '#4FA9FF'
          }
        },
        series: [
          {
            type: 'map',
            mapType: 'china',
            itemStyle: {
              normal: {
                areaColor: '#C1E0FF',
                borderColor: '#fff',
                borderWidth: 1,
                borderType: 'solid',
                label: {
                  show: true,
                  textStyle: {
                    color: '#000'
                  }
                }
              },
              emphasis: {
                areaColor: '#FFAA25',
                label: {
                  show: true
                }
              }
            },
            data: [
              { name: '北京', value: 3 },
              { name: '天津', value: 4 },
              { name: '上海', value: 1 },
              { name: '重庆', value: 3 },
              { name: '河北', value: 200 },
              { name: '河南', value: 1 },
              { name: '云南', value: 2 },
              { name: '辽宁', value: 1000 },
              { name: '黑龙江', value: 1 },
              { name: '湖南', value: 100 },
              { name: '安徽', value: 1000 },
              { name: '山东', value: 200 },
              { name: '新疆', value: 230 },
              { name: '江苏', value: 1 },
              { name: '浙江', value: 1200 },
              { name: '江西', value: 200 },
              { name: '湖北', value: 600 },
              { name: '广西', value: 140 },
              { name: '甘肃', value: 2 },
              { name: '山西', value: 200 },
              { name: '内蒙古', value: 500 },
              { name: '陕西', value: 2000 },
              { name: '吉林', value: 2 },
              { name: '福建', value: 70 },
              { name: '贵州', value: 1 },
              { name: '广东', value: 2 },
              { name: '青海', value: 100 },
              { name: '西藏', value: 100 },
              { name: '四川', value: 2000 },
              { name: '宁夏', value: 1 },
              { name: '海南', value: 2 },
              { name: '台湾', value: 1000 },
              { name: '香港', value: 1 },
              { name: '澳门', value: 2 }
            ]
          }
        ]
      }
      // window.onresize = chart.resize
      chart.setOption(option)
    }
  },
  mounted () {
    this.createChart()
  }
}
</script>
<style>
#listedMap {
  width: 80%;
  height: 100%;
}
</style>



