<template>
<div class="chart-wrap">
  <div id="chart6"></div>
  <div class="total"><span>3.24</span>亿元</div>
  <div class="radio-wrap">
    <label for="assets"><input type="radio" checked name="radio" id="assets" @change="radioChange(0)"><span></span>资产构成</label>
    <label for="Liabilities"><input type="radio" name="radio" id="Liabilities" @change="radioChange(1)"><span></span>负债构成</label>
  </div>
</div>
</template>

<script>
import echarts from 'echarts'
export default {
  data () {
    return {
      liaData: [
        { value: 2.5, name: '流动资产' },
        { value: 2.42, name: '固定资产' },
        { value: 0.76, name: '其他' },
        { value: 0.63, name: '无形资产' }
      ],
      elseData: [
        { value: 1.5, name: '流动资产' },
        { value: 2.42, name: '固定资产' },
        { value: 0.76, name: '其他' },
        { value: 2.63, name: '无形资产' }
      ],
      chart: null

    }
  },
  mounted () {
    this.createChart()
  },
  methods: {
    createChart () {
      this.chart = echarts.init(document.getElementById('chart6'))
      let paddingData = [3, -60, 45, -40]
      let rich = {
        yellow: {
          // color: '#ff7e00',
          fontSize: 14,
          padding: [0, 0, 0, 10],
          align: 'left'
        },
        black: {
          color: '#666666',
          fontSize: 12,
          padding: [0, 0, 0, -30],
          align: 'left'
        },
        number: {
          // color: '#ff7e00',
          fontSize: 14,
          // padding: [0, 10, 0, 10]
        },
        b: {
          fontSize: 12,
          align: 'right'
          // padding:[10],
        }
      }
      let option = {
        // tooltip: {
        //   show: false,
        //   formatter: function (params) {
        //     if (params.seriesIndex !== '0' && params.seriesIndex !== '1' && params.seriesIndex !== '2') {
        //       return '{a} <br/>{b} : {c} ({d}%)'
        //     }
        //   }
        // },
        backgroundColor: '#fff',
        // legend: {
        //   show: false,
        //   x: 20,
        //   y2: 20,
        //   orient: 'vertical',
        //   data: ["行政诉讼", "刑事诉讼", "民事诉讼"]
        // },
        color: ['#ff8c14', '#2092d6', '#03baff', '#3467b3', '#57d22c', '#ffc800'],
        calculable: false,
        series: [{
          name: '',
          type: 'pie',
          center: ['50%', '60%'],
          radius: ['35%', '60%'],
          label: {
            normal: {
              position: 'outside',
              show: true,
              formatter: function (params) {
                // return 'sdfsfsdfsdfsdfsdfsdfs'
                return '{yellow|' + params.percent + '%}\n' + params.name + '{number|' + params.value + '}亿元'
              },
              textStyle: {
                color: '#333',
                fontSize: 10
              },
              padding: paddingData,
              rich: rich
            },
            emphasis: {
              position: 'outside',
              show: true,
              textStyle: {
                color: '#333',
                fontSize: 10
              },
              rich: rich
            }
          },
          itemStyle: {
            normal: {
              borderColor: '#fff',
              borderWidth: 3
            }
          },
          labelLine: {
            normal: {
              textStyle: {
                fontSize: 10
              },
              length: 10,
              length2: 90,
              lineStyle: {
                width: 2,
                color: '#ebebeb'
              }
            },
            emphasis: {
              textStyle: {
                fontSize: 10
              },
              length: 10,
              length2: 70,
              lineStyle: {
                width: 2,
                color: '#ebebeb'
              }
            }
          },
          data: this.liaData
        }]
      }
      this.chart.setOption(option, true)
    },
    radioChange (flag) {
      var option = this.chart.getOption()
      if (flag) {
        option.series[0].data = this.elseData
      } else {
        option.series[0].data = this.liaData
      }
      this.chart.setOption(option, true)
    }
  }
}

</script>

<style lang="scss" scoped>
.chart-wrap {
  width: 100%;
  height: 100%;
  position: relative;
}
#chart6 {
  height: 100%;
}
.total {
  width: 18%;
  position: absolute;
  font-size: 14px;
  top: 53%;
  left: 41%;
  text-align: center;
  span {
    font-size: 18px;
    color: #ff8700;
  }
  &:after {
    content: '总计';
    display: block;
    border-top: 1px solid #ccc;
  }
}
.radio-wrap {
  position: absolute;
  right: 30px;
  top: 10px;
  line-height: 16px;
  label {
    width: 80px;
    height: 80px;
    display: inline-block;
    span {
      display: inline-block;
      float: left;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      border: 1px solid #ccc;
      box-sizing: border-box;
      margin-right: 5px;
    }
  }
  input[type='radio'] {
    display: none;
  }
  input[type='radio']:checked + span {
    border: 5px solid #175f85;
  }
}
</style>



