<template>
  <div id="chart"></div>
</template>
<script>
import echarts from 'echarts'
export default {
  data () {
    return {

    }
  },
  methods: {
    createChart () {
      let itemStyle = {
        normal: {
          borderColor: '#fff',
          borderWidth: 1
        }
      }
      let labelStyle = {
        normal: {
          show: false
        }
      }
      var chart = echarts.init(document.getElementById('chart'))
      let option = {
        color: ['#3261a6', '#1d98db', '#38c1ff', '#ffc500', '#ff8b13'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          },
          backgroundColor: 'rgba(255, 255, 255, 0.9)',
          formatter (param) {
            let html = ''
            html = '<div style="padding:10px 15px;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">'
            param.forEach(item => {
              html += '<p style="color:#333;">' + item.seriesName + '：' + '<span style="color:' + item.color + '">' + item.data + '</span></p>'
            })
            html += '</div>'
            return html
          }
        },
        legend: {
          data: ['案件受理公告', '其他公告', '执行公告', '判决公告', '开庭公告'],
          top: '5%'
        },
        grid: {
          top: '15%',
          bottom: '5%',
          containLabel: true
        },
        barWidth: 20,
        barGap: 10,
        xAxis: {
          type: 'value',
          axisLine: {
            lineStyle: {
              color: '#ccc'
            }
          },
          axisLabel: {
            color: '#666'
          },
          axisTick: {
            show: false
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: '#fff'
              }, {
                offset: 1,
                color: '#f3faff'
              }])
            }
          }
        },
        yAxis: {
          type: 'category',
          data: ['北京', '上海', '河北', '河南', '山东', '山西', '陕西'],
          axisLine: {
            lineStyle: {
              color: '#ccc'
            }
          },
          axisLabel: {
            color: '#666'
          },
          axisTick: {
            show: false
          },
        },
        series: [
          {
            name: '案件受理公告',
            type: 'bar',
            stack: '总量',
            label: labelStyle,
            data: [320, 302, 301, 334, 390, 330, 320]
          },
          {
            name: '其他公告',
            type: 'bar',
            stack: '总量',
            label: labelStyle,
            itemStyle: itemStyle,
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: '执行公告',
            type: 'bar',
            stack: '总量',
            label: labelStyle,
            itemStyle: itemStyle,
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: '判决公告',
            type: 'bar',
            stack: '总量',
            label: labelStyle,
            itemStyle: itemStyle,
            data: [150, 212, 201, 154, 190, 330, 410]
          },
          {
            name: '开庭公告',
            type: 'bar',
            stack: '总量',
            label: labelStyle,
            itemStyle: itemStyle,
            data: [820, 832, 901, 934, 1290, 1330, 1320]
          }
        ]
      }
      chart.setOption(option)
    }
  },
  mounted () {
    this.createChart()
  }
}
</script>
<style>
#chart {
  height: 100%;
}
</style>



