<template>
  <div class="example">
    <ul>
      <li v-for="item in 4" class="split" :style="{transform:'translate(-15px,125px) rotate('+item*45+'deg)'}"></li>
    </ul>
    <ul>
      <li v-for="(item,index) in outerData" class="outer" :style="{transform:'rotate('+(index*45+25)+'deg) translateY(125px)'}"><span :class="{'word-reverse':index>1&&index<6}">{{item}}</span></li>
    </ul>
    <div class="inner"></div>
    <div class="center">阿里巴巴</div>
    <ul>
      <li v-for="(item,index) in innerData" class="outer" :style="{transform:'rotate('+(index*45+25)+'deg) translateY(75px)'}">
        <span :style="{color:'#666',transform:'rotate('+(-index*45-25)+'deg)'}">{{item}}</span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data () {
    return {
      outerData: ['社交', '硬件', '网络安全', '文化教育', 'O2O', '电子商务', '其他', '交通物流'],
      innerData: ['华人文化', '58到家', '苏宁云商', '97公司', '圆通快递', 'snapch', 'microm', '瀚海苑']
    }
  }
}
</script>
<style scoped lang="scss">
.example {
  position: relative;
  margin: 22px auto 0;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  border: 5px solid #fff;
  box-shadow: 0 0 10px #d4ecfb;
  padding: 20px;
  box-sizing: border-box;
  background: rgba(54, 113, 196, 0.8);
  color: #fff;
  .split {
    transform: translateY(150px) rotate(30deg);
    height: 1px;
    width: 280px;
    background: #cccaca;
  }
  .outer {
    position: absolute;
    top: 45%;
    left: 42%;
    transform-origin: 50% 50%;
  }
  .word-reverse {
    display: inline-block;
    transform: rotate(-180deg);
  }
  .inner {
    width: 220px;
    height: 220px;
    border-radius: 50%;
    background: #fff;
    color: #666;
    margin: auto;
    margin-top: 10px;
  }
  .center {
    background: #ff8613;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -40px 0 0 -40px;
    line-height: 80px;
    text-align: center;
    border: 5px solid #fff;
    box-sizing: border-box;
    box-shadow: 0 0 20px #ffc8b3;
  }
  span {
    display: inline-block;
  }
}
</style>



