<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	export default {
		name: 'radarSingle',
		props: {
			echartsData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {
				indicator: [],
			}
		},
		watch: {
			echartsData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			dataTreat() {
				let arr = [];
				for(let item of this.echartsData.ind){
					let obj = {};
					obj.text = item;
					obj.max = 100;
					arr.push(obj);
				}
				this.indicator = arr;
			},
			initChart() {
				this.dataTreat();
				let typeShape = this.echartsData.type?this.echartsData.type:'circle'
				let option = {
					backgroundColor: '#fff',
					tooltip: {
            confine: true,
            trigger: 'item',
            backgroundColor:'#fff',
						textStyle: {
							color: '#666',
						},
						borderColor: 'rgba(211,221,231,.9)',
						borderWidth: 1,
						padding: 10
          },
					color: ['#015293'],
					// "legend": {
					// "orient": "vertical",
					// "x": "right",
					// "y": "top",
					// "data": [
					// "2016年8月",
					// "2016年9月"
					// ]
					// },
					calculable: true,
					radar: [{
						shape: typeShape,
						indicator: this.indicator,
//						indicator: [{
//								"text": "营收能力",
//								"max": 6000,
//							},
//							{
//								"text": "信用水品",
//								"max": 16000
//							},
//							{
//								"text": "资本结构",
//								"max": 30000
//							},
//							{
//								"text": "偿债能力",
//								"max": 38000
//							},
//							{
//								"text": "舆情分析",
//								"max": 52000
//							},
//							{
//								"text": "产品创新度",
//								"max": 52000
//							}
//						],
						name: {
							color: '#474747',
							fontSize: 14,
						},
						nameGap: 20,
						radius: '60%',
						splitNumber: 4,
						splitLine: {
							lineStyle: {
								width: 2,
								opacity: .3
							}
						},
						splitArea: {
							areaStyle: {
								color: ['rgba(200,200,200,0.4)', 'rgba(200,200,200,0.2)', 'rgba(200,200,200,0.2)', 'rgba(200,200,200,0.05)']
							}
						},
					}],
					series: [{
						type: "radar",
						data: [{
							name: this.echartsData.name,
							value: this.echartsData.value,
//							"value": [
//								4300,
//								10000,
//								28000,
//								35000,
//								50000,
//								40000
//							],
							areaStyle: {
								normal: {
									opacity: 0.1,
									color: '#015293'
								}
							},
							symbol: 'circle',
							symbolSize: 6,
							itemStyle: {
								color: '#2eb3ff'
							},
							lineStyle: {
								color: '#015293',
								opacity: 0.4,
							},
						}]
					}]
				}
				this.myChart.on('click', (params) => {
					this.$emit('handleCharts', params.name)
				})
				this.myChart.setOption(option);

			}

		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



