<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	export default {
		name: 'bar',
		props: {
			echartsData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {
				color: [],
				colorOp1: [],
				seriesData: [],
			}
		},
		watch: {
			echartsData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			dataTreat() {
				let color = this.echartsData.color;
				let colorA = [];
				let colorOp1 = [];
				for(let item of color) {
					colorA.push('rgb(' + item + ')');
					colorOp1.push('rgba(' + item + ', .5)');
				};
				this.color = colorA;
				this.colorOp1 = colorOp1;

				let data = [];
				for(let i in this.echartsData.name) {
					let obj = {
						type: 'line',
						symbolSize: 4
					}
					obj.name = this.echartsData.name[i];
					obj.data = this.echartsData.data[i];
					obj.areaStyle = {
						normal: {
							color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
									offset: 0,
									color: this.colorOp1[i]
								},
								{
									offset: 1,
									color: 'rgba(255,255,255,0)'
								}
							])
						}
					};
					data.push(obj);
				};
				this.seriesData = data;
			},
			initChart() {
				this.dataTreat();
				let option = {
					tooltip: {
						trigger: 'axis',
						backgroundColor:'transparent',

						padding: 10,
						formatter: function (params) {
              var html = '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);font-size:14px;font-weight:bold;"><p>'+params[0].axisValue+'</p>'
             for (let i = 0 ; i < params.length; i++ ) {
                  html+= '<p><span style="font-size:14px;font-weight:bold;">'+params[i].seriesName+'</span>：<span style="color:#ff7200;font-size:16px;">'+params[i].value+'</span></p>'
             }
              html += '<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i></div>'
               return html
            },
					},
					legend: {
						show: true,
						data: this.echartsData.name,
						left: 'center',
						top: '5',
						textStyle: {
							color: '#474747',
							fontSize: 12
						},
						itemWidth: 20,
						itemHeight: 10,
					},
					grid: {
						left: 20,
						right: 30,
						bottom: 20,
						top: 30,
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						boundaryGap: false,
						data: this.echartsData.time,
						axisLabel: {
							color: '#777',
							textStyle: {
								fontSize: 12
							}
						},
						axisLine: {
							show: true,
							lineStyle: {
								color: '#d6d7d8',
								width: 1,
							}
						},
						axisTick: {
							show: false
						},
						splitLine: {
							show: true,
							lineStyle: {
								color: '#edf2f4',
								type: 'solid',
								width: 1,
							}
						}
					}],
					yAxis: [{
						type: 'value',
						axisLabel: {
							color: '#777',
							textStyle: {
								fontSize: 12
							}
						},
						axisLine: {
							show: true,
							lineStyle: {
								color: '#d6d7d8',
								width: 1,
							}
						},
						axisTick: {
							show: false
						},
						splitLine: {
							lineStyle: {
								color: '#edf2f4',
								type: 'solid',
								width: 1,
							}
						},
						splitArea: {
							show: true,
							areaStyle: {
								color: ['#f8fbfd']
							}
						}
					}],
					color: this.color,
					series: this.seriesData,
					//					series: [{
					//							name: '主营业务收入',
					//							type: 'line',
					//							//							itemStyle: {
					//							//								normal: {
					//							//									color: '#366fc0',
					//							//								}
					//							//							},
					//							symbol: 'circle',
					//							symbolSize: 8,
					//							// 			smooth: true,
					//							data: [460, 532, 490, 584, 700, 660]
					//						},
					//						{
					//							name: '主营业务利润',
					//							type: 'line',
					//							//							itemStyle: {
					//							//								normal: {
					//							//									color: '#1aa0e1',
					//							//								}
					//							//							},
					//							symbol: 'circle',
					//							symbolSize: 6,
					//							areaStyle: {
					//								normal: {
					//									color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
					//											offset: 0,
					//											color: 'rgba(255,154,22,.5)'
					//										},
					//										{
					//											offset: 1,
					//											color: 'rgba(255,130,18,0)'
					//										}
					//									])
					//
					//								}
					//							},
					//							// 			smooth: true,
					//							data: [530, 542, 600, 534, 490, 470]
					//						},
					//						{
					//							name: '所有者所有者权益合计',
					//							type: 'line',
					//							//							itemStyle: {
					//							//								normal: {
					//							//									color: '#58d22c',
					//							//								}
					//							//							},
					//							symbol: 'circle',
					//							symbolSize: 6,
					//							// 			smooth: true,
					//							data: [320, 332, 360, 424, 530, 570]
					//						},
					//					]
				};
				this.myChart.setOption(option);

			}

		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>


