<template>
	<div class="mdlWrap">
		<h3 class="tit" :class="tit?tit:'active'">{{tit}}</h3>
		<slot></slot>
	</div>
</template>

<script type="text/javascript">
	export default {
		name: 'moduleWrap',
		props: {
	    tit: {
	      type: String,
	      default: '标题'
	    }
	 },
	}
</script>

<style lang="scss" scoped>
.mdlWrap{
	float: left;
	overflow: hidden;
	h3{
		display: block;
		border-left: 5px solid #015293;
		font-size: 16px;
		line-height: 20px;
		padding-left: 10px;
		margin: 30px 0 10px 0;
    height:20px;
    &.active{
      border:0;
      height:0;
    }
	}
	>div{
		background: #fff;
		float: left;
		border: 1px solid #e1e1e1;
		box-sizing: border-box;
		margin-right: 15px;
	}
}

</style>




