<template>
	<div class="echarts" ref="chart">

	</div>
</template>

<script>
	export default {
		name: 'accurateInvestment',
		data() {
			return {}
		},
		props: {
			echartsData: {
				type: Object,
				default: () => {}
			}
		},
		watch: {
			echartsData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let that = this
				let paddingData = this.echartsData.padding ? this.echartsData.padding : [3, -60, 45, -40]
				let rich = {
					yellow: {
//						color: '#ff7e00',
						fontSize: 14,
						padding: [-60, 0, 0, -20],
						lineHeight: 20,
						align: 'left'
					},
					black: {
						color: '#666666',
						fontSize: 12,
						padding: [-40, 0, 0, -20],
						align: 'left'
					}
				}
				let option = {
					tooltip: {
						show: false,
						formatter: function(params) {
							if(params.seriesIndex != "0" && params.seriesIndex != "1" && params.seriesIndex != "2") {
								return "{a} <br/>{b} : {c} ({d}%)";
							}
						}
					},
					backgroundColor: '#fff',
					legend: {
						show: false,
						x: 20,
						y2: 20,
						orient: 'vertical',
						data: ["行政诉讼", "刑事诉讼", "民事诉讼"]
					},
					color: ['#ff8c14', '#2092d6', '#03baff', '#3467b3', '#57d22c', '#ffc800'],
					calculable: false,
					series: [{
						name: '',
						type: 'pie',
						center: ['50%', '50%'],
						radius: ['30%', '50%'],
						label: {
							normal: {
								position: 'outside',
								show: true,
								formatter: function(params) {
									if(that.echartsData.type == 'number') {
										return '{black|' + params.value + '}\n{yellow|' + params.name + '}'
									} else {
										return '{black|' + params.value + '%}\n{yellow|' + params.name + '}'
									}
								},
								textStyle: {
									color: "#333",
									fontSize: 10
								},
								padding: paddingData,
								rich: rich
							},
//							emphasis: {
//								position: 'outside',
//								show: true,
//								formatter: function(params) {
//									if(that.echartsData.type == 'number') {
//										return '{black|' + params.value + '}\n{yellow|' + params.name + '}'
//										console.log(1)
//									} else {
//										return '{black|' + params.value + '%}\n{yellow|' + params.name + '}'
//									}
//								},
//								textStyle: {
//									color: "#333",
//									fontSize: 10
//								},
//								rich: rich
//							}
						},
						itemStyle: {
							normal: {
								borderColor: '#fff',
								borderWidth: 3
							}
						},
						labelLine: {
							normal: {
								textStyle: {
									fontSize: 10
								},
								length: 40,
								length2: 50,
								lineStyle: {
									width: 2,
									color: '#ebebeb'
								}
							},
							emphasis: {
								textStyle: {
									fontSize: 10
								},
								length: 40,
								length2: 50,
								lineStyle: {
									width: 2,
									color: '#ebebeb'
								}
							}
						},
						data: this.echartsData.seriesData
					}]
				}
				this.myChart.setOption(option)
			}
		},
		watch: {}
	}
</script>

<style lang="scss" scoped>
	.echarts {
		width: 100%;
		height: 100%;
	}
</style>


