<template>
	<div class="echarts" ref="chart">

	</div>
</template>

<script>
	export default {
		name: 'accurateInvestment',
		data() {
			return {}
		},
		props: {
			echartsData: {
				type: Object,
				default: () => {}
			}
		},
		watch: {
			echartsData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let that = this
				let option = {
					backgroundColor: '#fff',
					tooltip: {
						trigger: 'axis',
						confine: true,
						backgroundColor: 'transparent',
						formatter: function(params) {
							var html = '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);font-size:14px;font-weight:bold;"><p>' + params[0].axisValue + '</p>'
							for(let i = 0; i < params.length; i++) {
								html += '<p><span style="font-size:14px;font-weight:bold;">' + params[i].seriesName + '</span>：<span style="color:#ff7200;font-size:16px;">' + params[i].value + '</span></p>'
							}
							html += '<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i></div>'
							return html
						},
						padding: 10
					},
					legend: {
						show: true,
						data: this.echartsData.legendData,
						left: 'center',
						top: '5',
						textStyle: {
							color: '#333',
							fontSize: 10
						},
						itemWidth: 20,
						itemHeight: 10,
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '5%',
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						data: this.echartsData.xAxisData,
						axisLabel: {
							textStyle: {
								color: '#808080',
								fontSize: 12
							}
						},
						axisLine: {
							show: true,
							lineStyle: {
								color: '#d6d7d8',
								width: 1,
							}
						},
						axisTick: {
							show: false
						},
						splitLine: {
							lineStyle: {
								color: '#ededed'
							}
						}
					}],
					yAxis: [{
						name: this.echartsData.y1Name === false ? '':'单位（万）',
						nameTextStyle: {
							color: '#808080'
						},
						type: 'value',
						axisLabel: {
							textStyle: {
								color: '#808080',
								fontSize: 12
							}
						},
						axisLine: {
							show: true,
							lineStyle: {
								color: '#d6d7d8',
								width: 1,
							}
						},
						axisTick: {
							show: false
						},
						splitLine: {
							lineStyle: {
								color: '#edf2f4',
								type: 'solid',
								width: 1,
							}
						}
					}],
					color: ['#ff8d14', '#f5531a', '#58d22c', '#1aa0e1', '#366fc0'],
					series: [{
							name: this.echartsData.legendData[0],
							type: 'line',
							itemStyle: {
								normal: {
									//              color: '#ff8d14',
									borderColor: '#fff',
									borderWidth: '1',
								}
							},
							symbol: 'circle',
							symbolSize: 6,
							//          smooth: true,
							data: this.echartsData.seriesData[0]
						},
						{
							name: this.echartsData.legendData[1],
							type: 'line',
							itemStyle: {
								normal: {
									//                color: '#f5531a'
									borderColor: '#fff',
									borderWidth: '1',
								}
							},
							symbol: 'circle',
							symbolSize: 6,
							//            smooth: true,
							data: this.echartsData.seriesData[1]
						},
						{
							name: this.echartsData.legendData[2],
							type: 'line',
							itemStyle: {
								normal: {
									//                color: '#58d22c'
									borderColor: '#fff',
									borderWidth: '1',
								}
							},
							symbol: 'circle',
							symbolSize: 6,
							//            smooth: true,
							data: this.echartsData.seriesData[2]
						},
						{
							name: this.echartsData.legendData[3],
							type: 'line',
							itemStyle: {
								normal: {
									//                color: '#1aa0e1'
									borderColor: '#fff',
									borderWidth: '1',
								}
							},
							symbol: 'circle',
							symbolSize: 6,
							//            smooth: true,
							data: this.echartsData.seriesData[3]
						},
						{
							name: this.echartsData.legendData[4],
							type: 'line',
							itemStyle: {
								normal: {
									//                color: '#366fc0'
									borderColor: '#fff',
									borderWidth: '1',
								}
							},
							symbol: 'circle',
							symbolSize: 6,
							//            smooth: true,
							data: this.echartsData.seriesData[4]
						}
					]

				};
				this.myChart.setOption(option)
			}
		},
		watch: {}
	}
</script>

<style lang="scss" scoped>
	.echarts {
		width: 100%;
		height: 100%;
	}
</style>


