<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
        let that = this
        let paddingData = this.echartsData.padding?this.echartsData.padding:[3, -60, 45, -40]
        let rich = {
          yellow: {
            color:  '#ff7e00',
            fontSize: 14,
            padding: [0, 0, 0, -30],
            align: 'left'
          },
          black: {
            color: '#666666',
            fontSize: 12,
            padding: [0, 0, 0, -30],
            align: 'left'
          }
        }
        let option = {
          title: {
            text: this.echartsData.title,
            left: 'center',
            top: '5%',
            textStyle: {
              color: '#474747',
              fontSize: 14
            }
          },
          tooltip: {
            show: false,
            formatter: function(params) {
              if (params.seriesIndex != "0" && params.seriesIndex != "1" && params.seriesIndex != "2") {
                return "{a} <br/>{b} : {c} ({d}%)";
              }
            }
          },
          backgroundColor: '#fff',
          legend: {
            show: false,
            x: 20,
            y2: 20,
            orient: 'vertical',
            data: ["行政诉讼", "刑事诉讼", "民事诉讼"]
          },
          color: ['#ff8c14','#2092d6','#03baff','#3467b3','#57d22c','#ffc800'],
          calculable: false,
          series: [{
            name: '',
            type: 'pie',
            center: ['50%', '50%'],
            radius: ['25%', '45%'],
            label: {
              normal: {
                position: 'outside',
                show: true,
                formatter: function(params){
                  if (that.echartsData.type == 'number') {
                    return '{yellow|' + params.value + '}\n{black|' + params.name + '}'
                  }else{
                    return '{yellow|' + params.value + '%}\n{black|' + params.name + '}'
                  }
                },
                textStyle: {
                  color: "#333",
                  fontSize:10
                },
                padding: paddingData,
                rich: rich
              },
              emphasis:{
                position: 'outside',
                show: true,
                formatter: function(params) {
                    console.log(that.echartsData.type == 'number')
                    if (that.echartsData.type == 'number') {
                      return '{yellow|' + params.value + '}\n{black|' + params.name + '}'
                      console.log(1)
                    }else{
                      return '{yellow|' + params.value + '%}\n{black|' + params.name + '}'
                    }
                },
                textStyle: {
                  color: "#333",
                  fontSize:10
                },
                rich: rich
              }
            },
            itemStyle: {
              normal: {
                borderColor: '#fff',
                borderWidth: 3
              }
            },
            labelLine: {
              normal: {
                textStyle: {
                  fontSize: 10
                },
                length: 10,
                length2: 85,
                lineStyle: {
                  width: 2,
                  color:'#ebebeb'
                }
              },
              emphasis:{
                textStyle: {
                  fontSize: 10
                },
                length: 10,
                length2: 70,
                lineStyle: {
                  width: 2,
                  color:'#ebebeb'
                }
              }
            },
            data: this.echartsData.seriesData
          }]
        }
          this.myChart.setOption(option)
        }
    },
    watch: {
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:100%;
  }
</style>



