<template>
<div class="chart-wrapper" ref="chart" style="height:100%;width:100%"></div>
</template>

<script>
export default {
  name: 'wordGraph',
  props: {
    echartsData: {
      type: Object,
      default: () => { }
    }
  },
  data () {
    return {}
  },
  watch: {
    echartsData: {
      handler: function (val, oldVal) {
        this.initChart()
      },
      deep: true //增加deep 观察对象的子对象变化
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.myChart = this.$echarts.init(this.$refs.chart)
      this.initChart()
    })
  },
  methods: {
    initChart () {
      var colorList = {
        borderColors: ['#aa4a34', '#ae7622', '#063dbe', '#0074bf', '#735937'],
        fontColors: ['#fff'],
        bgColors: ['#3261a6', '#f66430', '#38c1ff', '#ffdc66', '#88daff', '#f99876', '#a5e981']
      }
      var data = [{
        name: '刘强东',
        value: 500
      },
      {
        'name': '京东商城',
        'value': 100
      },
      {
        'name': '京东三百六十度',
        'value': 100
      },
      {
        'name': '京东金融',
        'value': 50
      },
      {
        'name': '李亚云',
        'value': 200
      }
      ]


      function replace (str, length) {
        var reg = new RegExp('(\\S{' + length + '})', 'g')
        return str.replace(reg, '$1\n')
      }

      function chart01 (data) {
        //初始化政府渠道民意热点关键词
        var itemData = []
        var arry = []
        var totalNum = 0
        data.forEach(function (itm) {
          arry.push(itm.value)
          totalNum += parseInt(itm.value)
        })
        arry.sort(function (a, b) {
          return b - a
        }) //降序排列数组
        data.forEach(function (item, idx) {
          itemData.push({
            value: item.value,
            name: item.name,
            itemStyle: {
              normal: {
                // borderColor:colorList.borderColors[idx%5],
                // borderWidth:1,
                color: colorList.bgColors[idx % 7]
              },
              emphasis: {
                shadowColor: '#38c1ff',
                shadowBlur: 20
              }
            },
            label: {
              normal: {
                show: true,
                color: colorList.fontColors,
                fontSize: 10
              }
            }
          })
        })
        var myChart = createBubble({
          id: 'chart01',
          maxVal: arry[0],
          data: itemData,
          totalNum: totalNum
        })
        return myChart
      }

      function createBubble (param) {
        option = {
          backgroundColor: '#fff',
          tooltip: {
            show: false
          },
          grid: {
            // left: '5px',
            // top:'5px',
            // right: '5px',
            // bottom: '5px'
          },
          series: [{
            type: 'graph',
            layout: 'force',
            symbol: 'rRect',
            force: {
              repulsion: 100
            },
            symbolSize: function (val) {
              var width = (60 + val * 60 / param.maxVal * 0.9)
              var height = (20 + val * 30 / param.maxVal * 0.9)
              return [width, height]
            },
            roam: true,
            animation: true,
            draggable: true,
            label: {
              normal: {
                show: true,
                fontSize: 12,
                position: 'inside',
                formatter: function (bdItem) {
                  // var pt = (100 * bdItem.value / param.totalNum).toFixed(1)
                  return replace(bdItem.name, 7) + '  ' + bdItem.value
                }
              }
            },
            data: param.data
          }]
        }
        return option
      }

      let option = chart01(data)

      this.myChart.setOption(option)
      this.myChart.on('click', function (e) {
        console.log(e.name)
      })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



