<template>
  <div class="industry-chain-chart">
    <div class="top">
      <div class="block blue-block">上游企业/关联公司</div>
      <div class="double-dir-arrow">战略联盟</div>
      <div class="block sky-block">核心企业</div>
      <div class="double-dir-arrow">战略联盟</div>
      <div class="block blue-block">上游企业/关联公司</div>
    </div>
    <div class="border-block">产业链<br/>金融服务</div>
    <div class="middle-arrow-left">大力支持网络借款</div>
    <div class="middle-arrow-right">项目收益交易信息</div>
    <div class="side-arrow-left"><span>优惠</span></div>
    <div class="side-arrow-right"><span>优惠</span></div>
    <div class="center">xxx</div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      data: {
        top: '上游企业/关联公司',
        bot: '下游企业/关联公司'
      }
    }
  }
}
</script>
<style scoped lang="scss">
.industry-chain-chart {
  position: relative;
  padding: 20px 0 0 100px;
  color: #333;
  .block {
    width: 98px;
    height: 68px;
    color: #fff;
    font-size: 16px;
    margin: 5px;
    float: left;
    border-radius: 5px;
    text-align: center;
    padding: 0 5px;
    box-sizing: border-box;
  }
  .blue-block {
    box-sizing: border-box;
    border: 4px solid #ebf1fa;
    background: #346ab7;
    line-height: 24px;
    padding-top: 5px;
  }
  .sky-block {
    background: #31c0ff;
    height: 60px;
    line-height: 60px;
    margin-top: 9px;
  }
  .border-block {
    width: 116px;
    height: 60px;
    padding-top: 4px;
    line-height: 24px;
    box-sizing: border-box;
    border: 1px solid #3263aa;
    text-align: center;
    position: absolute;
    left: 13px;
    top: 130px;
    border-radius: 5px;
  }
  .double-dir-arrow {
    height: 36px;
    line-height: 36px;
    border-bottom: 1px solid #585858;
    position: relative;
    float: left;
    margin-top: 5px;
    padding: 0 5px;
    &:before,
    &:after {
      content: '';
      position: absolute;
      border: 4px solid transparent;
      bottom: -4px;
    }
    &:before {
      border-right-color: #585858;
      left: -4px;
    }
    &:after {
      border-left-color: #585858;
      right: -4px;
    }
  }
  .middle-arrow-left {
    border-right: 1px solid #585858;
    padding: 15px 10px;
    writing-mode: tb-rl;
    position: absolute;
    left: 240px;
    top: 110px;
    width: 60px;
    height: 90px;
    box-sizing: border-box;
    &:before {
      content: '';
      position: absolute;
      border: 4px solid transparent;
      border-bottom-color: #585858;
      top: -4px;
      right: -4px;
    }
  }
  .middle-arrow-right {
    border-left: 1px solid #585858;
    padding: 15px 10px;
    writing-mode: tb-rl;
    position: absolute;
    left: 320px;
    top: 110px;
    width: 60px;
    height: 90px;
    box-sizing: border-box;
    &:before {
      content: '';
      position: absolute;
      border: 4px solid transparent;
      border-top-color: #585858;
      bottom: -4px;
      left: -4px;
    }
  }
  .side-arrow-left {
    border-right: 1px solid #585858;
    padding: 0 5px;
    position: absolute;
    left: 180px;
    top: 110px;
    width: 40px;
    height: 150px;
    line-height: 100px;
    box-sizing: border-box;
    transform: rotate(-45deg);
    &:before {
      content: '';
      position: absolute;
      border: 4px solid transparent;
      border-bottom-color: #585858;
      top: -4px;
      right: -4px;
    }
    span {
      display: inline-block;
      transform: rotate(45deg);
    }
  }
  .side-arrow-right {
    border-left: 1px solid #585858;
    padding: 0 5px;
    position: absolute;
    left: 430px;
    top: 110px;
    width: 40px;
    height: 150px;
    line-height: 100px;
    box-sizing: border-box;
    transform: rotate(45deg);
    &:before {
      content: '';
      position: absolute;
      border: 4px solid transparent;
      border-bottom-color: #585858;
      top: -4px;
      left: -4px;
    }
    span {
      display: inline-block;
      transform: rotate(-45deg);
    }
  }
  .center {
    width: 136px;
    height: 66px;
    line-height: 66px;
    text-align: center;
    border: 6px solid #fff;
    border-radius: 50%;
    background: #ff9615;
    box-shadow: 0 10px 20px #ffcdba;
    position: absolute;
    left: 256px;
    bottom: 32px;
  }
}
</style>



