<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
        let that = this
        let option = {
          backgroundColor: '#fff',
          tooltip: {
            trigger: 'axis',
            backgroundColor: 'transparent',
            formatter: function(param) {
              var html= '';
              return html += '<div style="text-align:center;position:relative;width:50px; height:30px;line-height:30px;background:#fff;border:1px solid #28affc;border-radius:5px;color:#7a7a7a;box-shadow:0 0 10px 0 rgba(40,157,252,.6);">'+
                '<span style="font-size:16px;color:#14aaff;font-weight:bold;">'+param[0].value+'</span>'+
                '<i style="position:absolute;bottom:-12px;left:19px;width:0;height:0;border:6px solid transparent;border-top:6px solid #28affc;"></i>'+
                '<i style="position:absolute;bottom:-9px;left:20px;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i>'+
                '</div>'
            }
          },
          legend: {
            show: false
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '10%',
            top: '15%',
            containLabel: true
          },

          xAxis: {
            boundaryGap: false,
            axisLabel: {
              textStyle: {
                color: '#828282',
                fontSize: 12
              },
              margin: 12
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#d6d7d8',
                width: 2,
              }
            },
            axisTick:{
              show:false
            },
            data: this.echartsData.xAxisData
          },
          yAxis: {
            name: '(%)',
            type: 'value',
            axisLabel: {
              textStyle: {
                color:'#828282',
                fontSize: 12
              }
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#d6d7d8',
                width: 2,
              }
            },
            axisTick:{
              show:false
            },
            splitLine:{
              lineStyle:{
                color:'#edf2f4',
                type:'solid',
                width: 1,
              }
            }
          },
          color:['#14aaff'],
          series: [
            {
              name:'',
              type:'line',
              markArea:{
                silent:true,
                data:[
                  [{
                    name: '',
//                    xAxis: this.echartsData.xAxisData[-1],
                    itemStyle: {
                      color: new that.$echarts.graphic.LinearGradient(
                        0, 0, 0, 1,
                        [
                          {offset: 0, color: 'rgba(255,255,255,.5)'},
                          {offset: 1, color: 'rgba(229,243,255,.5)'}
                        ]
                      )
                    },
                  }, {
//                    xAxis: this.echartsData.xAxisData[6],
                  }]
                ]
              },
              smooth: true,
              symbol: 'circle',
              symbolSize: 6,
              lineStyle: {
                normal: {
                  width: 1
                }
              },
              data: this.echartsData.seriesData
            }
          ]
        };
          this.myChart.setOption(option)
        }
    },
    watch: {
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:100%;
  }
</style>



