<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	export default {
		name: 'analyzePie',
		props: {
			echartsData: {
				type: Array,
				default: []
			}
		},
		data() {
			return {}
		},
		watch: {
			echartsData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let option = {
					backgroundColor: '#fff',
					color: ['#ff9400', '#3371ca', '#1894f9', '#00bbff', '#54d512', '#ffca00'],
					tooltip: {
						trigger: 'item',
						formatter: "{d}%",
						backgroundColor: 'transparent',
						position: ['42%', '42%'],
						padding: 10,
						textStyle: {
							fontSize: '20',
							color: '#000'
						}
					},
					series: [
						{
							name: '',
							type: 'pie',
							radius: ['40', '70'],
							center: ['50%', '50%'],
							data: this.echartsData,
//							data: [{
//									value: 90,
//									name: '竞争分析',
//								}, {
//									value: 290,
//									name: '环境分析'
//								}, {
//									value: 280,
//									name: '机构分析'
//								},
//								{
//									value: 130,
//									name: '企业专家'
//								}, {
//									value: 70,
//									name: '本体分析'
//								},
//								{
//									value: 140,
//									name: '企业家'
//								}
//							],
							itemStyle: {
								normal: {
									borderColor: '#fff',
									borderWidth: 2
								}
							},
							label: {
								normal: {
									position: 'outside',
									show: true,
									// 	formatter:"{d}%\n{b}\n\n\n", 
									formatter: function(params) {
										var html = '';
										return '{b|' + params.value + '}' + '\n{a|' + params.name + '}';
									},
									textStyle: {
										fontSize: 14
									},

									rich: {
										a: {
											color: '#7a7a7a',
											fontSize: 13,

											// align: 'center',
										},
										b: {
											fontSize: 13,
											// padding:[10],
										},

									}

								},
								labelLine: {
									normal: {
										length: 10,
										length2: 70,
									}
								}
							}
						}
					]
				};

				this.myChart.setOption(option);

			}

		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>


