<template>
  <div class="echarts" ref="chart">

  </div>
</template>

<script>
  export default {
    name: 'accurateInvestment',
    data() {
        return {}
    },
    props: {
      echartsData: {
        type: Object,
        default: () => {}
      }
    },
    watch: {
      echartsData: {
        handler: function(val, oldVal) {
          this.initChart()
        },
        deep: true //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart()  {
        let that = this
        let maxArr = this.echartsData.seriesData[0].concat(this.echartsData.seriesData[1]).concat(this.echartsData.seriesData[2])
        let maxData = maxArr.sort(function(a,b){
            return b-a
        })[0]*1.2
        let xAxisData =  this.echartsData.xAxisData
        let option = {
          backgroundColor: '#fff',
          tooltip: {
            trigger: 'axis',
            backgroundColor: 'transparent',
            axisPointer: {
              type: 'shadow',
              shadowStyle: {
                color: '#eeeff1',
              },
              z: 0
            },
            formatter: function (params) {
              var html = '<div style="text-align:center;position:relative;padding:10px;background:#fff;text-align:left;color:#666;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);font-size:14px;font-weight:bold;"><p>'+params[0].axisValue+'</p>'
              for (let i = 0 ; i < params.length; i++ ) {
                html+= '<p><span style="font-size:14px;font-weight:bold;">'+params[i].seriesName+'</span>：<span style="color:#ff7200;font-size:16px;">'+params[i].value+'</span></p>'
              }
              html += '<i style="position:absolute;bottom:-9px;left:48%;width:0;height:0;border:5px solid transparent;border-top:5px solid #fff;"></i></div>'
              return html
            }
          },
          calculable: true,
          legend: {
            top:40,
            left: 'center',
            itemGap: 30,
            orient: 'horizontal',
            textStyle: {
              color: '#4e78d9'
            },
            itemWidth: 14,
            itemHeight: 9,
            data:this.echartsData.legendData,
          },
          xAxis: [{
            type: 'category',
            data: xAxisData,
            axisLine: {
              lineStyle: {
                color: '#ccc',
                width: 1
              }
            },
            nameRotate:10,
            axisTick: {
              show: false
            },
            axisLabel: {
              rotate: 45,
              interval: 0,
              margin: 15,
              formatter: function(params) {
                if(params.length > 9) {
                  return  params.slice(0, 9) + '...'
                }else{
                  return  params
                }
              },
              textStyle: {
                color: '#808080'
              },
              triggerEvent: true
            },
            splitLine: {
              show: false,

            },
          }],
          color: ['#2489cf', '#38c1ff', '#ff8b13'],
          yAxis: [{
            min: 0,
            max: maxData,
            type: 'value',
            name: '单位（家）',
            nameLocation: 'end',
            nameGap:20,
            nameTextStyle: {
              color: '#6a6a6a',
              fontSize: 12,
            },
            axisLabel: {
              formatter: '{value}',
              textStyle: {
                color: '#808080'
              }
            },
            axisLine: {
              lineStyle: {
                color: '#ccc',
                width: 1
              }
            },
            splitLine: {
              show: true,
            },
            axisTick: {
              show: false
            },
          }],
          grid: {
            left: '4%',
            right: '4%',
            bottom: '6%',
            top: '110',
            containLabel: true
          },
          series: [{
            name: '2012',
            type: 'bar',
            barWidth: 6,
            // barGap:1,
            data: this.echartsData.seriesData[0],
            label: {
              normal: {
                show: false,
              }
            },
            markArea: {
              silent: true,
              data: [
                [{
                  name: '',
                  xAxis: xAxisData[-1],
                  itemStyle: {

                      color: new that.$echarts.graphic.LinearGradient(
                        0, 0, 0, 1,
                        [
                          {offset: 0, color: 'rgba(255,255,255,0.5)'},
                          {offset: 1, color: 'rgba(229,243,255,0.5)'}
                        ]
                      )
                  },
                }, {
                  xAxis: xAxisData[xAxisData.length],
                }]

              ]
            }
          }, {
            name: '2013',
            type: 'bar',
            barWidth: 6,
            data: this.echartsData.seriesData[1],
            label: {
              normal: {
                // formatter:'{c}',
                textStyle: {
                  color: '#fff'
                }
              }
            }
          }, {
            name: '2014',
            type: 'bar',
            barWidth: 6,
            data: this.echartsData.seriesData[2],
            label: {
              normal: {
                show: false,
              }
            }
          }]
        };

        this.myChart.setOption(option)
        }
    }
  }
</script>

<style lang="scss" scoped>
  .echarts{
    width: 100%;
    height:100%;
  }
</style>



