<template>
  <div id="chart1"></div>
</template>
<script>
import echarts from 'echarts'
export default {
  data () {
    return {}
  },
  methods: {
    createChart () {
      var chart = echarts.init(document.getElementById('chart1'))
      let xAxisData = ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
      let option = {
        // backgroundColor: '#fff',
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow',
            shadowStyle: {
              color: '#eeeff1'
            },
            z: 0
          },
          backgroundColor: 'rgba(255, 255, 255, 0.9)',
          formatter (param) {
            let html = ''
            html = '<div style="padding:10px 15px;border:1px solid rgba(211,221,231,.9);border-radius:5px;box-shadow:0 0 10px 0 rgba(211,221,231,.9);">'
            param.forEach(item => {
              html += '<p style="color:#333;">' + item.seriesName + '：' + '<span style="color:' + item.color + '">' + item.data + '</span></p>'
            })
            html += '</div>'
            return html
          }
        },
        calculable: true,
        legend: {
          top: 40,
          left: 'center',
          itemGap: 30,
          orient: 'horizontal',
          textStyle: {
            color: '#4e78d9'
          },
          itemWidth: 14,
          itemHeight: 9,
          data: ['市场竞争力', '市场集中度', '市场占有率', '股票持有率']
        },
        yAxis: [{
          min: 0,
          max: '2500',
          type: 'value',
          // name: '单位（家）',
          nameLocation: 'end',
          nameGap: 20,
          nameTextStyle: {
            color: '#6a6a6a',
            fontSize: 12
          },
          axisLabel: {
            formatter: '{value}',
            textStyle: {
              color: '#808080'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#ccc',
              width: 1
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#ccc',
              width: 1
            }
          },
          axisTick: {
            show: false
          },
          z: 1
        }],
        xAxis: [{
          type: 'category',
          data: xAxisData,
          axisLine: {
            lineStyle: {
              color: '#ccc',
              width: 1
            }
          },
          nameRotate: 10,
          axisTick: {
            show: false
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: '#fff'
              }, {
                offset: 1,
                color: '#f3faff'
              }]),
              z: 0
            }
          },
          axisLabel: {
            // rotate:45,
            interval: 0,
            margin: 15,
            formatter: function (params) {
              if (params.length > 9) {
                return params.slice(0, 9) + '...'
              } else {
                return params
              }
            },
            textStyle: {
              color: '##808080'
            },
            triggerEvent: true
          },
          splitLine: {
            show: false

          }
        }],
        color: ['#1f94d8', '#32c1ff', '#ffcb00', '#ff8b14'],

        grid: {
          left: '4%',
          right: '5%',
          bottom: '10%',
          top: '110',
          containLabel: true
        },
        series: [{
          name: '市场竞争力',
          type: 'bar',
          barWidth: 6,
          // barGap:1,
          data: [1300, 1420, 1321, 1321, 214, 1650, 1700, 1120, 1321, 1321, 214, 1700],
          label: {
            normal: {
              show: false
            }
          }
        }, {
          name: '市场集中度',
          type: 'bar',
          barWidth: 6,
          data: [1200, 1232, 999, 888, 123, 150, 182, 1120, 1150, 1200, 1232, 182],
          label: {
            normal: {
              // formatter:'{c}',
              textStyle: {
                color: '#fff'
              }
            }
          }
        }, {
          name: '市场占有率',
          type: 'bar',
          barWidth: 6,
          data: [1200, 1232, 999, 888, 123, 150, 182, 1120, 1150, 1200, 1232, 999],
          label: {
            normal: {
              show: false
            }
          }
        }, {
          name: '股票持有率',
          type: 'bar',
          barWidth: 6,
          data: [1200, 1232, 999, 888, 123, 150, 182, 1120, 1150, 1200, 1232, 999],
          label: {
            normal: {
              show: false
            }
          }
        }]
      }
      chart.setOption(option)
    }
  },
  mounted () {
    this.createChart()
  }
}
</script>
<style>
#chart1 {
  height: 100%;
}
</style>



